import axios from 'axios';
import React from 'react';

/**
 * Turns string boolean into real boolean.
 *
 * @param {string} s - A string that may be 'true'.
 * @returns {boolean} True if 'true'.
 */
export function toBool(s) {
    return s === 'true' ? true : false;
}

const partialMatchFn = (m) => (s) => s.indexOf(m) < 0,
    fullMatchFn = (m) => (s) => s !== m;

/**
 * Remove a class name from an HTML element.
 *
 * @param {HTMLElement} elem - An HTML element.
 * @param {string} className - A string representing the class to remove.
 * @param {boolean} [removePartials] - If true, remove partially matching classes.
 */
export function removeClass(elem, className, removePartials) {
    const matchFn = removePartials
        ? partialMatchFn(className)
        : fullMatchFn(className);
    elem.className = elem.className
        .split(/\s+/)
        .filter((s) => matchFn(s))
        .join(' ');
}

/**
 * Detect whether reduced motion is enabled.
 *
 * If it is, you should shortcut animations, disable autoplay elements, avoid
 * smooth scrolling in favour of jump scrolling, etc.
 *
 * Currently unsupported by Chrome, but works in Safari and Firefox.
 *
 * @returns {boolean} Boolean indicating that the reduce motion media query matches.
 */
export function reduceMotion() {
    const reducedMotionQuery = window.matchMedia(
        '(prefers-reduced-motion: reduce)'
    );
    return reducedMotionQuery.matches ? true : false;
}

/**
 * Predicate testing whether an element is currently visible, as opposed to
 * displayed: none; or similar. Doesn't necessarily mean it's visible in the
 * window.
 *
 * @param {HTMLElement} elem - An HTML element.
 * @returns {boolean} - Is it visible?
 */
export function isVisible(elem) {
    return elem.offsetHeight !== 0 && elem.offsetWidth !== 0 ? true : false;
}

/**
 * Predicate testing whether an element is positioned in the window.
 *
 *
 * @param {HTMLElement} elem - An HTML element.
 * @returns {boolean} - Is it onscreen?
 */
export function verticallyInWindow(elem) {
    return elem.getBoundingClientRect().top >= 0 &&
        elem.getBoundingClientRect().top <= window.innerHeight
        ? true
        : false;
}

/**
 * Turn a query string into an object.
 *
 * @param {string} parameterString - An HTML query string.
 * @returns {object} An object containing the parameters.
 */
export function parametersToObject(parameterString) {
    const parameters = {};
    parameterString
        .substr(1)
        .split('&')
        .forEach((parameter) => {
            const [k, v] = parameter.split('=');
            parameters[k] = v;
        });
    return parameters;
}

/**
 * Turn an object into a query string.
 *
 * @param {object} parameterObj - An object containing the parameters and their values.
 * @returns {string} A string of the parameters.
 */
export function objectToParameters(parameterObj) {
    if (Object.keys(parameterObj).length > 0) {
        let parameters = '?';
        for (const key in parameterObj) {
            parameters.length !== 1 && (parameters += '&');
            parameters += key + '=' + parameterObj[key];
        }
        return parameters;
    } else {
        return '';
    }
}

/**
 * Track a Google Analytics event.
 *
 * @param {string} event - The event key for Tag Manager.
 * @param {string} eventCategory - The event category.
 * @param {string} eventAction - The event action.
 * @param {string} [eventLabel] - The event label.
 * @param {number} [eventValue] - The event value.
 * @param {boolean} [nonInteractionHit] - Does this count as interacting with the page?
 */
export function gaEvent(
    event,
    eventCategory,
    eventAction,
    eventLabel,
    eventValue,
    nonInteractionHit
) {
    window.dataLayer = window.dataLayer || [];
    window.dataLayer.push({
        event: event,
        eventCategory: eventCategory,
        eventAction: eventAction,
        eventLabel: eventLabel,
        eventValue: eventValue,
        nonInteractionHit: nonInteractionHit,
    });
}

/**
 * Append an array of elements to an element.
 *
 * @param {HTMLElement} elem - The parent element.
 * @param {HTMLElement[]} children - An array of elements to append to it.
 */
export function appendAll(elem, children) {
    children.forEach((child) => elem.appendChild(child));
}

/**
 * Extract number from string, e.g. return '123' from string 'a1b2c3'.
 *
 * @param {string} string - String
 */
export function numberFromString(string) {
    let number = string.replace(/\D/g, '');
    return number;
}

/**
 * Calculates viewport width to determinte if using mobile
 * @param {number} number - width of screen, with default value = 432.
 *
 */
export function isMobile(screen = 432) {
    const viewPortWidth = window.innerWidth;
    const mobileScreen = screen;

    if (viewPortWidth < mobileScreen) {
        return true;
    }
}

/**
 *
 * Turn DOM elements to array e.g return [li, li, li] from DOM element selecting 'li' <ul><li></li><li></li><li></li></ul>
 *
 * @param {object} object - Object
 */

export function toArray(obj) {
    let array = [];
    // iterate backwards ensuring that length is an UInt32
    for (let i = obj.length >>> 0; i--; ) {
        array[i] = obj[i];
    }
    return array;
}

/**
 * Check browser user agent is IE or edge and return version number
 */
export function detectIE() {
    const ua = window.navigator.userAgent,
        versionLength = 10,
        msie = ua.indexOf('MSIE '),
        msieVersionOffset = 5;

    if (msie > 0) {
        // IE 10 or older => return version number
        return parseInt(
            ua.substring(msie + msieVersionOffset, ua.indexOf('.', msie)),
            versionLength
        );
    }

    const trident = ua.indexOf('Trident/'),
        tridentVersionOffset = 3;

    if (trident > 0) {
        // IE 11 => return version number
        let rv = ua.indexOf('rv:');
        return parseInt(
            ua.substring(rv + tridentVersionOffset, ua.indexOf('.', rv)),
            versionLength
        );
    }

    const edge = ua.indexOf('Edge/'),
        edgeVersionOffset = 5;

    if (edge > 0) {
        // Edge (IE 12+) => return version number
        return parseInt(
            ua.substring(edge + edgeVersionOffset, ua.indexOf('.', edge)),
            versionLength
        );
    }

    // other browser
    return false;
}

/**
 * Check if browser supports IntersectionObserver API
 *
 * @returns {Boolean} -
 */
export function checkIntersectionObserver() {
    if (
        'IntersectionObserver' in window &&
        'IntersectionObserverEntry' in window &&
        'intersectionRatio' in window.IntersectionObserverEntry.prototype
    ) {
        return true;
    } else {
        return false;
    }
}

/**
 * Create elements containing classes, content and any other attributes
 *
 * @param {string} type - Type of element to create, e.g. "div", "button".
 * @param {array} attributes - Array of objects specifying which attributes to assign to the element i.e. {label : "class", val : "container"}
 */
export function createHTMLElement(type, attributes) {
    let el = document.createElement(type);
    attributes.map(function (att) {
        att.label === 'content'
            ? el.appendChild(document.createTextNode(att.val))
            : el.setAttribute(att.label, att.val);
    });
    return el;
}

export function uppercaseFirstLetterLowercaseRest(s) {
    return s.charAt(0).toUpperCase() + s.slice(1).toLowerCase();
}

/**
 * Asynchronous request to Funnelback web service.
 *
 * @param {object} config An axios configuration object.
 * @return {Promise} - A promise of a Funnelback response.
 */
export function axiosRequest(config) {
    const httpOK = 200;

    return axios(config)
        .then((response) => {
            if (httpOK === response.status) {
                return response.data;
            } else {
                throw `Bad response: ${response.status}`;
            }
        })
        .catch((e) => {
            if (!axios.isCancel(e)) {
                gaEvent(
                    'jsError',
                    'JavaScript error',
                    `Line ${e.lineNumber}  ${e.message}`,
                    `axiosRequest ${e.fileName} (${window.location})`,
                    true
                );
            }
        });
}

/**
 * Returns the correct English suffix for a number.
 *
 * @param  {number} day - The day of the month.
 * @returns {string} The correct suffix.
 */
function daySuffix(day) {
    const eleventh = 11,
        thirteenth = 13,
        lastDigit = day.toString().slice(-1);

    return day >= eleventh || day <= thirteenth
        ? 'th'
        : lastDigit === '1'
        ? 'st'
        : lastDigit === '2'
        ? 'nd'
        : lastDigit === '3'
        ? 'rd'
        : 'th';
}

/**
 * Format a date for React.
 *
 * @param {Date} date - A date object.
 * @returns {ReactFragment} The formatted date.
 */
export function formatReactDate(date) {
    const months = [
            'January',
            'February',
            'March',
            'April',
            'May',
            'June',
            'July',
            'August',
            'September',
            'October',
            'November',
            'December',
        ],
        days = [
            'Sunday',
            'Monday',
            'Tuesday',
            'Wednesday',
            'Thursday',
            'Friday',
            'Saturday',
        ];

    return (
        <time dateTime={date.toISOString().split('T')[0]}>
            {days[date.getUTCDay()]}, {date.getUTCDate()}
            <sup>{daySuffix(date.getUTCDate())}</sup>{' '}
            {months[date.getUTCMonth()]} {date.getUTCFullYear()}
        </time>
    );
}

/**
 * Array slicer
 *
 * @param {array} arr - array to be split up into subarrays
 * @param {number} len - max number items that goes into each subarray
 */
export function arraySlicer(arr, len) {
    let newArray = [],
        i = 0,
        n = arr.length;

    while (i < n) {
        newArray.push(arr.slice(i, (i += len)));
    }
    return newArray;
}
